import { h, Component } from 'preact';
import { connect } from 'preact-redux';
import store from "../store";
import Svg from "../svg";

class Result extends Component {
  constructor() {
    super();

    this.restart = this.restart.bind(this);
  }

  restart() {
    store.dispatch({
      type: 'TEST_RESTART',
    });
  }

  render(props, state) {

    return (
      <div className="psb-result">
        <div className="psb-result__header">
          <div className="psb-q__logo" dangerouslySetInnerHTML={{ __html: Svg.logo }} />
        </div>
        <div className="psb-result__body">
          <div className="psb-result__title">Я исправил {props.test.correctAnswers}<br/> кусков кода</div>
          <div className="psb-result__restart" onClick={this.restart}>Пройти еще раз</div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = function(store) {
  return {
    test: store.testState
  };
}

export default connect(mapStateToProps)(Result);