import { h } from 'preact';
import store from "../store";
import Data from '../data';
import Svg from "../svg";

export default () => {
  const start = () => {
    store.dispatch({
      type: 'TEST_STATUS',
      status: 'START',
    });
  };

  return (
    <div className="psb-enter">
      <div className="psb-enter__header">
        <div className="psb-q__logo" dangerouslySetInnerHTML={{ __html: Svg.logo }} />
      </div>
      <div className="psb-enter__body">
        <div className="psb-enter__title" dangerouslySetInnerHTML={{ __html: Data.title }} />
        <div className="psb-enter__text">{Data.description}</div>
        <button className="psb-enter__start-btn" onClick={start}>Начать</button>
      </div>
    </div>
  );
}