import { h, Component } from 'preact';
// import ReactDOM from 'react-dom';
import { connect } from 'preact-redux';
import Data from '../data';
import Svg from '../svg';
import code from '../code/1.code';
import store from "../store";

class Question extends Component {
  constructor() {
    super();

    this.answer = this.answer.bind(this);
    this.next = this.next.bind(this);
    this.result = this.result.bind(this);
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (this.state.answered !== undefined) {
      const blocks = this.code.querySelectorAll('span');

      const msg = document.createElement('div');
      msg.classList.add('psb-code__msg');

      if (this.state.answered === this.question.correct) {
        blocks[this.state.answered].classList.add('is-correct');
        msg.classList.add('is-correct');
        msg.innerHTML = this.question.correctMsg;
      } else {
        blocks[this.state.answered].classList.add('is-active');
        blocks[this.question.correct].classList.add('is-correct');
        msg.classList.add('is-incorrect');
        msg.innerHTML = this.question.incorrectMsg;
      }

      blocks[this.state.answered].appendChild(msg);
    }
  }

  answer(e) {
    if (this.state.answered !== undefined) return;

    if (e.target.tagName.toLowerCase() === 'span') {
      const block = e.target;
      const answerIndex = [...block.parentElement.children].indexOf(block);

      store.dispatch({
        type: 'TEST_ANSWER',
        isCorrect: answerIndex === this.question.correct,
      });

      this.setState({
        answered: answerIndex
      });
    }
  }

  next() {
    this.setState({
      answered: undefined,
    });

    store.dispatch({
      type: 'TEST_NEXT',
    });
  }

  result() {
    this.setState({
      answered: undefined,
    });

    store.dispatch({
      type: 'TEST_STATUS',
      status: 'RESULT',
    });
  }

  render(props, state) {
    this.question = Data.questions[props.test.activeIndex];
    const getButton = () => {
      console.log(props.test.activeIndex, Data.questions.length);
      if (props.test.activeIndex < Data.questions.length - 1) {
        return <button className="psb-q__btn" onClick={this.next}>Далее</button>
      } else {
        return <button className="psb-q__btn" onClick={this.result}>Результат</button>
      }
    };

    return (
      <div className="psb-q">
        <div className="psb-q__header">
          <div className="psb-q__pager">{`${props.test.activeIndex + 1}/${Data.questions.length}`}</div>
          <div className="psb-q__logo" dangerouslySetInnerHTML={{ __html: Svg.logo }} />
        </div>
        <div className="psb-q__head">
          <div className="psb-q__text" dangerouslySetInnerHTML={{ __html: this.question.text }} />
        </div>
        <div className="psb-q__code">
          <div className="psb-q__code-inner" onClick={this.answer}>
            <pre className="psb-q__code-pre" ref={code => this.code = code} dangerouslySetInnerHTML={{ __html: code }} />
          </div>
        </div>
        {
          state.answered !== undefined ?
            getButton()
            : null
        }
      </div>
    );
  }
}

const mapStateToProps = function(store) {
  return {
    test: store.testState
  };
}

export default connect(mapStateToProps)(Question);