import { createStore, combineReducers } from 'redux';

const initialTestState = {
  status: '',
  params: {},
  questions: {},
  activeIndex: 0,
  correctAnswers: 0,
};

const restartTestState = {
  status: 'START',
  activeIndex: 0,
  correctAnswers: 0,
};

const testReducer = function(state = initialTestState, action) {
  switch (action.type) {
    case 'TEST_PARAMS':
      return { ...state, params: action.params };
    case 'TEST_STATUS':
      return { ...state, status: action.status };
    case 'TEST_ANSWER':
      return { ...state, correctAnswers: action.isCorrect ? state.correctAnswers + 1 : state.correctAnswers };
    case 'TEST_NEXT':
      return { ...state, activeIndex: state.activeIndex + 1 };
    case 'TEST_RESTART':
      return { ...state, ...restartTestState };
  }
  return state;
};

const reducers = combineReducers({
  testState: testReducer,
});

export default createStore(reducers);